@import "utility/mixins";
@import "utility/variables";
@import "utility/functions";

*,
*::before,
*::after {
	-webkit-box-sizing: border-box;
	box-sizing: border-box; }

html {
	overflow-y: auto; }

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

h1 {
	font-size: 40px;
	font-weight: 700;
	text-align: center;
	margin-bottom: 50px; }

h2 {
	font-size: 30px;
	font-weight: 600;
	text-align: center; }

p {
	font-weight: 400;
	line-height: 25px; }

ul {
	margin: 0;
	list-style: none;
	padding: 0; }

#return-to-top {
	display: none;
	position: fixed;
	bottom: 20px;
	right: 20px;
	z-index: $toTop-z;
	width: 50px;
	height: 50px;
	border-radius: 35px;
	text-align: center;
	background: transparentize($black, .3);
	i {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(calc(-50% - 2px));
		color: $white;
		font-size: 19px;
		transition: all .2s; }
	&:hover {
		background: transparentize($black, .15);
		i {
			top: 12px; } } }

body {
	font-family: 'Lato', sans-serif;
	position: relative;
	z-index: $body-z; }

a, a:hover {
	color: inherit;
	text-decoration: none; }

h3 {
	font-size: 20px;
	font-weight: 400; }

main {
	position: relative;
	color: $white;
	z-index: $main-z;
	h1 {
		padding-top: calc(100px + 80px);
		padding-bottom: 20px;
		margin-bottom: 100px;
		text-transform: uppercase;
		position: relative;
		text-shadow: 0 1px 2px transparentize($black, .95), 0 2px 4px transparentize($black, .8);
		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			height: 3px;
			width: rem(50px);
			background-color: $white;
			box-shadow: 0 1px 2px transparentize($black, .8); } } }

.waves {
	div {
		position: absolute;
		background-position: center bottom;
		background-repeat: no-repeat;
		background-size: auto;
		// background-size: contain
		height: 100%;
		width: 100%; } }

.columns {
	margin-left: 0;
	margin-right: 0;
	margin-top: 0;
	&:not(:last-child) {
		margin-bottom: 0; }
	&:last-child {
		margin-bottom: 0; }
	&:after {
		display: table;
		content: '';
		clear: both; } }

.column {
	padding: 0; }

.container {
	max-width: 80%; }

.row {
	margin-left: 0px;
	margin-right: 0px;
	&[class^=col] {
		margin: 0; } }

.modal-content {
	overflow: hidden; }

// UTILITY

.hide {
	display: none !important; }

.svg-red {
	fill: $red !important;
	stroke: $red !important; }

.svg-red-shadow {
	@include filter (drop-shadow(0px 0px 3px $red)); }

.svg-darkBlue {
	fill: #1B5EDC !important; }

.svg-lightBlue {
	fill: #19A2FF !important; }

.svg-purple {
	fill: #BC2A8D !important; }

.text-red {
	color: $red !important; }

.logo-rotate {
	@include transform (rotate(450deg) !important); }

.closeButton-rotate {
	@include transform (rotate(90deg) translateX(0.5px) translateY(1px));
	opacity: 0.5; }

.footer-afterOpen {
	margin-bottom: 70px; }

.footer-afterClose {
	margin-bottom: 0; }

.overflow-hidden {
	overflow: hidden; }
